<template lang="html">
    <x-header>
      参赛队伍
      <a slot="right" v-link="{name: 'teamsnew'}">
        创建队伍
      </a>
    </x-header>
    <div class="weui_panel weui_panel_access">
      <div class="weui_panel_hd">参赛队伍一共{{teams.length}}队</div>
      <div class="weui_panel_bd">
        <a v-for="team in teams" v-link="{name: 'team', params: {tid: team.id}}" class="weui_media_box weui_media_appmsg">
          <div class="weui_media_hd">
            <img :src="team.logo.logo.url" class="weui_media_appmsg_thumb" /></div>
          <div class="weui_media_bd">
            <h4 class="weui_media_title">{{team.name}}</h4>
            <p class="weui_media_desc">{{team.sign}}</p>
          </div>
        </a>
      </div>
    </div>
    <toast :show.sync="loadFail" type="text">加载失败</toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Toast from 'vux/dist/components/toast'

  export default {
    data: function () {
      return {
        teams: [],
        loadFail: false
      }
    },
    route: {
      data (transition) {
        this.$http.get(this.$config.API + '/teams.json').then(function (response) {
          // 登录成功
          this.$set('teams', response.data)
        }, function (response) {
          // 登录失败
          this.$set('loadFail', true)
        })
      }
    },
    methods: {},
    components: {
      XHeader,
      Toast
    }
  }
</script>

<style lang="scss">
</style>



